import React, {useState, useEffect} from 'react';
import './personal-location.css';
import addIcon from './img/add.png';
import biographyIcon from './img/biography.png';
import editIcon from './img/pencil.png';

export default function PersonalBio(props) {
	const [isAdd, setIsAdd] = useState(false);
	const [isComplete, setIsComplete] = useState(false);
	
	let initialTxt = "";
	if (sessionStorage.bio) {
		initialTxt = sessionStorage.bio;
	}
	const [inputTxt, setInputTxt] = useState(initialTxt);

	let display;

	const handleAdd = () => {
		setIsAdd(true);
	}

	const handleCancel = () => {
		if(!sessionStorage.bio) {
			setInputTxt("");
		} else {
			setInputTxt(sessionStorage.bio);
		}
		setIsAdd(false);
	}

	const handleSave = () => {
		sessionStorage.bio = inputTxt;
		if(inputTxt) {
			setIsAdd(false);
			setIsComplete(true);
		} else {
			setIsAdd(false);
			setIsComplete(false);
		}
	}

	const handleChange = e => {
		setInputTxt(e.target.value);
	}

	const handleEdit = () => {
		setIsAdd(true);
	}

	useEffect(() => {
		if (sessionStorage.bio) {
			setIsComplete(true);
		}
	}, []);

	const addElements = (<div className="edit-section-wrapper">
												 <textarea placeholder="About You" value={inputTxt} onChange={handleChange} autoFocus />
											   <div>
											     <button id="save" onClick={handleSave}>Save</button>
											     <button id="cancel" onClick={handleCancel}>Cancel</button>
											   </div>
											 </div>);

	const finalElements = (<>
												   <img src={biographyIcon} alt="biography" />
												   <p>{sessionStorage.bio}</p>
												   <span className="edit-icon-wrapper">
												   		<img src={editIcon} alt="edit" onClick={handleEdit} />	
												   </span> 
												 </>);

	const initialElements = (<div className="initial-wrapper" onClick={handleAdd}>
											   	   <img src={addIcon} alt="add" />
											   	   <span>Add bio</span>
											 		 </div>);

	if (isAdd) {
		display = addElements;		
	} else if (isComplete) {
		display = finalElements;
	} else {
		display = initialElements;
	}

	return (
		<div className="wrapper">
			{display}
		</div>
	);
}