import React from 'react';
import './sign-in-form.css';

class Form extends React.Component {
	constructor(props){
		super(props);
		this.state = {
			name: "",
			email: "",
			showTip: false
		};
		this.submitButton = React.createRef();
		this.inputName = React.createRef();
		this.inputEmail = React.createRef();
		this.inputPassword = React.createRef();
	}

	handleChange = e => {
		if(e.target.id === "nickname") {
			this.setState({ name: e.target.value });
		} else if (e.target.id === "email") {
			this.setState({ email: e.target.value });
		} 
	}

	handleFocus = () => {
		this.setState({showTip: true});
	}

	handleClick = e => {
		if ((e.target !== this.inputName.current) && (e.target !== this.inputEmail.current) && (e.target !== this.inputPassword.current) && (e.target !== this.submitButton.current)) {
			this.setState({showTip: false});
		}
	}

	login = e => {
		e.preventDefault();
		if (window.sessionStorage) {
			sessionStorage.nickname = this.state.name;
			sessionStorage.email = this.state.email;
		} else {
			alert("Please update your browser to the newest version. Thank you!");
		}
		this.props.clickSubmit(true);
	}

	render(){
		return (
			<div className="form-wrapper" onClick={this.handleClick}>
				<svg className="logo-signin">
					<path className="icon-lock" d="M18 8h-1V6c0-2.76-2.24-5-5-5S7 3.24 7 6v2H6c-1.1 0-2 .9-2 2v10c0 1.1.9 2 2 2h12c1.1 0 2-.9 2-2V10c0-1.1-.9-2-2-2zM9 6c0-1.66 1.34-3 3-3s3 1.34 3 3v2H9V6zm9 14H6V10h12v10zm-6-3c1.1 0 2-.9 2-2s-.9-2-2-2-2 .9-2 2 .9 2 2 2z"></path>
				</svg>
				<h1>Sign in</h1>
				<form className="form-signin" method="POST" action="#" onSubmit={this.login}>
	         	<input id="nickname" className="input-name" type="text" placeholder="Nickname" name="nickname" value={this.state.name} onChange={this.handleChange} onFocus={this.handleFocus} ref={this.inputName} required />
	         	<input id="email" className="input-email" type="email" placeholder="Email Address" name="email" value={this.state.email} onChange={this.handleChange} onFocus={this.handleFocus} ref={this.inputEmail} required />
	         	<input id="password" className="input-password" type="password" placeholder="Password" name="password" onFocus={this.handleFocus} ref={this.inputPassword} required /> 
	         	{this.state.showTip ? (<p>You are free to input arbitrarily on these blanks to sign in</p>) : null}
	         	<button type="submit" ref={this.submitButton}>SIGN IN</button>
				</form>
			</div>
		);
	}
}

export default Form;
