import React, {useState, useEffect} from 'react';
import './personal-location.css';
import addIcon from './img/add.png';
import locationIcon from './img/location.png';
import editIcon from './img/pencil.png';

export default function PersonalLocation(props) {
	const [isAdd, setIsAdd] = useState(false);
	const [isComplete, setIsComplete] = useState(false);
	const [options, setOptions] = useState(null);
	
	let initialOption = "initialSelect";
	if (sessionStorage.location) {
		initialOption = sessionStorage.location;
	} 
	const [optionSelected, setOptionSelected] = useState(initialOption);
	
	let display;

	const handleAdd = () => {
		setIsAdd(true);
	}

	const handleCancel = () => {
		if(!sessionStorage.location){
			setOptionSelected("initialSelect");
		} else {
			setOptionSelected(sessionStorage.location);
		}
		setIsAdd(false);
	}

	const handleSave = () => {
		if(optionSelected !== "initialSelect") {
			sessionStorage.location = optionSelected;
			setIsAdd(false);
			setIsComplete(true);
		} else {
			setIsAdd(false);
		}
	}

	const handleChange = e => {
		setOptionSelected(e.target.value);
	}

	const handleEdit = () => {
		setIsAdd(true);
	}

	useEffect(() => {
		fetch(process.env.PUBLIC_URL + '/country-list.json')
			.then(response => response.json())
			.then(
				data => {
					setOptions(data.countries.map(country => 
						<option key={country.code} value={country.name}>{country.name}</option>
					));
				},
				error => alert(error)
			);
	}, []);

	useEffect(() => {
		if (sessionStorage.location) {
			setIsComplete(true);
		}
	}, []);

	const addElements = (<div className="edit-section-wrapper">
												 <select value={optionSelected} onChange={handleChange}>
												   <option value="initialSelect" disabled>Select a country ...</option>
												   {options}
												 </select>
											   <div>
											     <button id="save" onClick={handleSave}>Save</button>
											     <button id="cancel" onClick={handleCancel}>Cancel</button>
											   </div>
											 </div>);

	const finalElements = (<>
												   <img src={locationIcon} alt="location" />
												   <span>{sessionStorage.location}</span>
												   <span className="edit-icon-wrapper">
												   		<img src={editIcon} alt="edit" onClick={handleEdit} />	
												   </span> 
												 </>);

	const initialElements = (<div className="initial-wrapper" onClick={handleAdd}>
											   	   <img src={addIcon} alt="add" />
											   	   <span>Add location</span>
											 		 </div>);

	if (isAdd) {
		display = addElements;		
	} else if (isComplete) {
		display = finalElements;
	} else {
		display = initialElements;
	}

	return (
		<div className="wrapper">
			{display}
		</div>
	);
}