import React, {useRef} from 'react';
import {NavLink} from 'react-router-dom';
import './horizontal-nav.css';
import Logo from './img/deer-white.png';

export default function HorizontalNav(props) {
	const inputCheckbox = useRef(null);

	const handleClick = () => {
		inputCheckbox.current.checked = false;
	}

	return (
			<div className="header">
			  <input type="checkbox" className="openSidebarMenu" id="openSidebarMenu" ref={inputCheckbox} />
			  <label htmlFor="openSidebarMenu" className="sidebarIconToggle">
			    <div className="spinner line-1"></div>
			    <div className="spinner line-2"></div>
			    <div className="spinner line-3"></div>
			  </label>
			  <img src={Logo} alt="logo" />
			<div id="sidebarMenu">
			  <ul className="sidebarMenuInner" onClick={handleClick} >
			    <NavLink to="/home"><li><h2>Home</h2></li></NavLink>
			    <NavLink to="/profile"><li><h2>Profile</h2></li></NavLink>
			  </ul>
			</div>
		</div>
	);
}