import React, {useState, useRef} from 'react';
import './personal-info.css';
import nameIcon from './img/name.png';
import editIcon from './img/pencil.png';
import confirmIcon from './img/confirm.png';
import closeIcon from './img/close.png';

export default function PersonalName(props){
	const [isEdit, setIsEdit] = useState(false);
	const [inputTxt, setInputTxt] = useState(sessionStorage.nickname); 
	const nameInput = useRef(null);

	const handleEdit = () => {
		setIsEdit(true);
	}

	const handleChange = e => {
		setInputTxt(e.target.value);
	}

	const inputValidation = () => {
		if (nameInput.current.validity.valueMissing === true) {
			nameInput.current.setCustomValidity("The name field can't be blank");
			nameInput.current.reportValidity();
			return false;
		} else {
			return true;
		}
	}

	const handleClose = () => {
		setInputTxt(sessionStorage.nickname);
		setIsEdit(false);
	}

	const handleConfirm = () => {
		if(inputValidation()){
			sessionStorage.nickname = inputTxt;
			props.re(); // invoke the reRender function in the parent(profile.js) component and no need to pass any parameter to it. Notice that "()" is a must since "re" only refers to the reRender function definition rather than invoking it 
			setIsEdit(false);
		}
	}

	const editElements = (<>
													<input type="text" value={inputTxt} onChange={handleChange} ref={nameInput} required />
													<img src={closeIcon} alt="close" onClick={handleClose} />
													<img src={confirmIcon} alt="confirm" onClick={handleConfirm} />
												</>);

	const initialElements = (<>
														 <span>{sessionStorage.nickname}</span> 			
														 <span>
														   <img src={editIcon} alt="edit" onClick={handleEdit} />
														 </span>
													 </>);

	return (
		<div className="clearfix">
			<img src={nameIcon} alt="nickname" />
			{isEdit ? editElements : initialElements} 
		</div>
	);
}