import React, {useState, useEffect} from 'react';
import {useHistory} from 'react-router-dom';
import './App.css';
import Form from './sign-in-form';
import Main from './main';

function App(props) {
  const [directFlag, setDirectFlag] = useState(false);
  const history = useHistory();

  function submit(isDirect){
    history.push("/");
    setDirectFlag(isDirect);
  }

  const clearSession = () => {
    sessionStorage.clear();
  }

  useEffect(() => {
    window.addEventListener('unload', clearSession);
    return () => {
      window.removeEventListener('unload', clearSession);
    };
  }, []);

  return (
    <div className="App">
      {directFlag ? <Main /> : 
        (<main>
          <div className="landing-left"></div>
          <div className="landing-right"><Form clickSubmit={submit} /></div>
        </main>)}
    </div>
  );
}

export default App;
