import React from 'react';
import './tweet-posted.css';
import Reply from './reply';
import TweetResponse from './tweet-response';
import {responses} from './main';

class TweetPosted extends React.Component {
	constructor(props){
		super(props);
		this.state = {
			like_counter: sessionStorage.getItem(this.props.index + "LikeStatus") ? 1 : 0,
			like_flag: sessionStorage.getItem(this.props.index + "LikeStatus") ? true : false,
			like_fill: sessionStorage.getItem(this.props.index + "LikeStatus") ? "rgb(206, 59, 96)" : "rgb(91, 112, 131)",
			comment_counter: responses[props.index] ? responses[props.index].length : 0,
			comment_flag: false,
			comment_fill: "rgb(91, 112, 131)",
			comment_bg: "",
			comment_text: "",
			timePassed: sessionStorage.getItem(this.props.number) ? parseInt(sessionStorage.getItem(this.props.number)) : 0,
			numChildren: responses[props.index] ? responses[props.index].length : 0
		};
		this.children = responses[props.index] ? responses[props.index] : [];
		this.timer = null;
	}

	addComment = () => {
		if (this.state.comment_flag) {
			this.setState({
				comment_flag: !this.state.comment_flag,
				comment_fill: "rgb(91, 112, 131)",
				comment_bg: ""
			});
		} else {
			this.setState({
				comment_flag: !this.state.comment_flag,
				comment_fill: "rgb(74, 161, 235)",
				comment_bg: "rgb(237, 245, 253)"
			});
		}
	}

	addLike = () => {
		if (this.state.like_flag) {
			this.setState({
				like_counter: this.state.like_counter - 1,
				like_flag: !this.state.like_flag,
				like_fill: "rgb(91, 112, 131)"
			});
		} else {
			this.setState({
				like_counter: this.state.like_counter + 1,
				like_flag: !this.state.like_flag,
				like_fill: "rgb(206, 59, 96)"
			});
		}
	}

	onAddComment = text => {
		this.children.unshift(<TweetResponse key={Math.random()} number={Math.random()} value={text} index={this.state.numChildren} />);
		this.setState({
			comment_flag: false,
			comment_text: text,
			comment_fill: "rgb(91, 112, 131)",
			comment_bg: "",
			comment_counter: this.state.comment_counter + 1,
			numChildren: this.state.numChildren + 1
		});
	}

	componentDidMount = () => {
		this.timer = setInterval(() => {
			this.setState({timePassed: this.state.timePassed + 1});
		}, 1000);
	}

	componentWillUnmount = () => {
		clearInterval(this.timer);
		this.props.responsesUp(this.props.index, this.children);
		sessionStorage.setItem(this.props.number, this.state.timePassed);
		if (this.state.like_flag) {
			sessionStorage.setItem(this.props.index + "LikeStatus", "true");
		} else {
			sessionStorage.removeItem(this.props.index + "LikeStatus");
		}
	}

	shouldComponentUpdate = (nextProps, nextState) => {
		if(nextState.timePassed !== this.state.timePassed) {
			if ((nextState.timePassed === 0) || (nextState.timePassed === 15) || (nextState.timePassed === 30) || (nextState.timePassed === 45)) {
				return true;
			} else if (nextState.timePassed%60 === 0) {
				return true;
			} else {
				return false;
			}
		} else {
			return true;
		}
	}

	render(){
		var style = {
			likeIcon: {
				fill: this.state.like_fill
			},
			commentIcon: {
				fill: this.state.comment_fill,
				backgroundColor: this.state.comment_bg
			}
		};

		const tweets = this.children.map((child) => child);

		let reply_comment = null;
		if(this.state.comment_flag){
			reply_comment = <Reply addCom={this.onAddComment} />;
		} 

		let imgLink = "https://avatars.dicebear.com/4.5/api/initials/" + sessionStorage.nickname + ".svg?backgroundColorLevel=900&chars=1&background=%234aa1eb";

		let timeDisplay;
		if (this.state.timePassed < 60) {
			timeDisplay = this.state.timePassed + "s";
		} else if (this.state.timePassed >= 60) {
			timeDisplay = Math.floor(this.state.timePassed/60) + "m";
		} 

		return (
			<div>
				<div className="tweets-area">
					<div className="tweets-left">
						<img className="avatars" src={imgLink} alt="avatar" />
					</div>
					<div className="tweets-right">
						<strong>{sessionStorage.nickname}</strong><span>{timeDisplay}</span>
						<p>{this.props.userInput}</p>
						<div className="icon-bottom">
							<div className="icon-comment">
								<svg style={style.commentIcon} onClick={this.addComment}><g><path d="M14.046 2.242l-4.148-.01h-.002c-4.374 0-7.8 3.427-7.8 7.802 0 4.098 3.186 7.206 7.465 7.37v3.828c0 .108.044.286.12.403.142.225.384.347.632.347.138 0 .277-.038.402-.118.264-.168 6.473-4.14 8.088-5.506 1.902-1.61 3.04-3.97 3.043-6.312v-.017c-.006-4.367-3.43-7.787-7.8-7.788zm3.787 12.972c-1.134.96-4.862 3.405-6.772 4.643V16.67c0-.414-.335-.75-.75-.75h-.396c-3.66 0-6.318-2.476-6.318-5.886 0-3.534 2.768-6.302 6.3-6.302l4.147.01h.002c3.532 0 6.3 2.766 6.302 6.296-.003 1.91-.942 3.844-2.514 5.176z"></path></g></svg>
								<span>{this.state.comment_counter}</span>
							</div>
							<div className="icon-like">
								<svg style={style.likeIcon} onClick={this.addLike}><g><path d="M12 21.638h-.014C9.403 21.59 1.95 14.856 1.95 8.478c0-3.064 2.525-5.754 5.403-5.754 2.29 0 3.83 1.58 4.646 2.73.814-1.148 2.354-2.73 4.645-2.73 2.88 0 5.404 2.69 5.404 5.755 0 6.376-7.454 13.11-10.037 13.157H12zM7.354 4.225c-2.08 0-3.903 1.988-3.903 4.255 0 5.74 7.034 11.596 8.55 11.658 1.518-.062 8.55-5.917 8.55-11.658 0-2.267-1.823-4.255-3.903-4.255-2.528 0-3.94 2.936-3.952 2.965-.23.562-1.156.562-1.387 0-.014-.03-1.425-2.965-3.954-2.965z"></path></g></svg>
								<span>{this.state.like_counter}</span>
							</div>
						</div>
					</div>
				</div>				
				{reply_comment}
				{this.state.comment_counter ? tweets : null}
			</div>
		)
	}

}

export default TweetPosted;

