import React, {useState, useRef, useEffect} from 'react';
import './cover-photo.css';
import photo from './img/fujisan.jpg';
import cameraIcon from './img/camera.png';

export default function CoverPhoto(props) {
	const fileInput = useRef(null);
	const [bgImage, setBgImage] = useState(`url(${photo})`);
	let fileURL, imgLink;

	const handleClick = () => {
		if (fileInput) {
			fileInput.current.click();
		} 
	}

	const handleChange = e => {
		let fileSelected = e.target.files[0];
		fileURL = window.URL.createObjectURL(fileSelected);
		sessionStorage.coverPhotoURL = fileURL;
		setBgImage(`url(${fileURL})`);
	}

	useEffect(() => {
		URL.revokeObjectURL(fileURL);
	}, [fileURL]);

	const style = {
			photoWrapper: {
				backgroundImage: sessionStorage.coverPhotoURL ? (`url(${sessionStorage.coverPhotoURL})`) : bgImage
			}
		}

	imgLink = "https://avatars.dicebear.com/4.5/api/initials/" + sessionStorage.nickname + ".svg?backgroundColorLevel=900&chars=1&background=%234aa1eb";

	return (
		<div style={style.photoWrapper} className="cover-photo-wrapper">
			<img className="avatar" src={imgLink} alt="avatar" />
			<input type="file" accept=".jpg, .jpeg, .png, .webp" name="imgPicker" ref={fileInput} onChange={handleChange} />
			<button onClick={handleClick}>
				<img src={cameraIcon} alt="camera" />
				<span>Change Cover Photo</span>
			</button>
		</div>
	);
}
