import React, {useState, useRef} from 'react';
import './personal-info.css';
import emailIcon from './img/email.png';
import editIcon from './img/pencil.png';
import confirmIcon from './img/confirm.png';
import closeIcon from './img/close.png';

export default function PersonalEmail(props){
	const [isEdit, setIsEdit] = useState(false);
	const [inputTxt, setInputTxt] = useState(sessionStorage.email); 
	const emailInput = useRef(null);

	const handleEdit = () => {
		setIsEdit(true);
	}

	const handleChange = e => {
		setInputTxt(e.target.value);
	}

	const inputValidation = () => {
		if (emailInput.current.validity.valueMissing === true) {
			emailInput.current.setCustomValidity("The email field can't be blank");
			emailInput.current.reportValidity();
			return false;
		} else {
			return true;
		}
	}

	const handleClose = () => {
		setInputTxt(sessionStorage.email);
		setIsEdit(false);
	}

	const handleConfirm = () => {
		if(inputValidation()){
			sessionStorage.email = inputTxt;
			setIsEdit(false);
		}
	}

	const editElements = (<>
													<input type="email" value={inputTxt} onChange={handleChange} ref={emailInput} required />
													<img src={closeIcon} alt="close" onClick={handleClose} />
													<img src={confirmIcon} alt="confirm" onClick={handleConfirm} />
												</>);

	const initialElements = (<>
														 <span>{sessionStorage.email}</span> 
														 <span>			
														 	<img src={editIcon} alt="edit" onClick={handleEdit} />
														 </span>
													 </>);

	return (
		<div className="clearfix">
			<img src={emailIcon} alt="email" />
			{isEdit ? editElements : initialElements} 
		</div>
	);
}