import React from 'react';
import './tweet.css';
import 'emoji-mart/css/emoji-mart.css';
import { Picker } from 'emoji-mart';

class Tweet extends React.Component {
	constructor(props){
		super(props);
		this.state = {
			text: "",
			showEmoji: false,
			pickerBgColor: null
		};
	}

	toggleEmojiPicker = () => {
    	this.setState({
      		showEmoji: !this.state.showEmoji
    	});
  }

	handleChange = e => {
		this.setState({
			text: e.target.value
		});
		this.textInput.style.height = "";
		this.textInput.style.height = this.textInput.scrollHeight + "px";
	}

	handleSubmit = e => {
		e.preventDefault();
		this.props.addChild(this.state.text);
		this.setState({
			text: ""
		});
	}

	addEmoji = e => {
    let emoji = e.native;
    let curPos = this.textInput.selectionStart;
    //console.log(curPos);
    this.setState({
      text: this.state.text.slice(0, curPos) + emoji + this.state.text.slice(curPos),
      showEmoji: false
    });
    this.textInput.focus();
  };

  	componentDidUpdate = (prevProps, preState) => {
  		if (preState.showEmoji !== this.state.showEmoji) {
	  		if (this.state.showEmoji) {
	    		this.setState({
	    			pickerBgColor: "rgb(237, 245, 253)"
	    		});
	    	} else {
	    		this.setState({
	    			pickerBgColor: null
	    		});
	    	}
    	}
  	}

	render(){

		let imgLink = "https://avatars.dicebear.com/4.5/api/initials/" + sessionStorage.nickname + ".svg?backgroundColorLevel=900&chars=1&background=%234aa1eb"; //backgroundColors[]=amber

		const style = {
			picker: {
				backgroundColor: this.state.pickerBgColor
			}
		};
		
		return (
			<div className="tweet-main">
				<div className="tweet-area">
					<div className="tweet-left">
						<img className="avatar" src={imgLink} alt="avatar" />
					</div>
					<div className="tweet-right">
						<form className="form-tweet" method="POST" action="#" onSubmit={this.handleSubmit} >
							<textarea name="tweet" rows="1" placeholder="What's happening?" value={this.state.text} onChange={this.handleChange} ref={el => (this.textInput = el)} autoFocus /><hr />
							<div className="bottomButtons">
	          					<svg style={style.picker} onClick={this.toggleEmojiPicker} >
	          						<g><path d="M12 22.75C6.072 22.75 1.25 17.928 1.25 12S6.072 1.25 12 1.25 22.75 6.072 22.75 12 17.928 22.75 12 22.75zm0-20C6.9 2.75 2.75 6.9 2.75 12S6.9 21.25 12 21.25s9.25-4.15 9.25-9.25S17.1 2.75 12 2.75z"></path><path d="M12 17.115c-1.892 0-3.633-.95-4.656-2.544-.224-.348-.123-.81.226-1.035.348-.226.812-.124 1.036.226.747 1.162 2.016 1.855 3.395 1.855s2.648-.693 3.396-1.854c.224-.35.688-.45 1.036-.225.35.224.45.688.226 1.036-1.025 1.594-2.766 2.545-4.658 2.545z"></path><circle cx="14.738" cy="9.458" r="1.478"></circle><circle cx="9.262" cy="9.458" r="1.478"></circle></g>
	          					</svg>
								<button type="submit" disabled={!this.state.text}>Tweet</button>
							</div>
						</form>
						{this.state.showEmoji ? (<span className="picker" ref={el => (this.emojiPicker = el)}><Picker set="apple" onSelect={this.addEmoji} emojiTooltip={true} title="Pick your emoji…" include={["recent", "smileys", "people", "food", "nature"]} /></span>) : null}
					</div>
				</div>
				{this.props.children}
			</div>
		);
	}
}

export default Tweet;


